import speech from '../assets/images/speech.svg'
import { useState } from "react"



export default function Speech ({setSpeech}) {

  const [isProcessing, setIsProcessing] = useState(false)

  //
  if (!('webkitSpeechRecognition' in window)) {
    return console.log('')
  } 
  
  const recognition = new window.webkitSpeechRecognition()
  recognition.continuous = false
  recognition.interimResults = true
  recognition.lang="cmn-Hant-TW";
  //
  recognition.onstart = () => {
    console.log('')
  }
  //
  recognition.onresult = (event) => {
    const result = event.results[0][0].transcript

    setSpeech(result)
    setIsProcessing(false)
  }
  //
  recognition.onerror = (event) => {
    console.log(': ', event)
    setIsProcessing(false)
    //
  }
  //
  recognition.onend = () => {
    console.log('')
  }

  if (isProcessing) return (
    <button 
      className="speech isProcessing">
      {/* <img className="speech__img" src={speech} alt="speech"></img> */}
      <div className='speech__isProcessing'>
        <div className="line"></div>
        <div className="line"></div>
        <div className="line"></div>
      </div>
    </button>
  )
  return (
    <button 
      className="speech"
      onClick={() => {
      if (isProcessing) return
      setIsProcessing(true)
      recognition.start()
    }}>
      <img src={speech} alt="speech"></img>
    </button>
  )
}